import styled from "styled-components";

export const FormAddStyledContainer = styled.div`
 .mainForm {
  width: 500px;
  margin: 0 auto;
  padding: 10px;
  border: 7px solid rgb(111, 136, 247);
  border-radius: 7px;
 }
 .inputContainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
 }
 .labelName {
  display: block;
  margin-bottom: 20px;
  font-size: 25px;
  font-weight: 500;
 }
 .inputName {
  display: block;
  max-width: 200px;
  height: 30px;
  margin-bottom: 30px;
  padding-left: 10px;
  border-radius: 5px;
  border: 1px solid rgba(33, 33, 33, 0.2);

  :focus {
   outline: 4px solid #2196f3;
   cursor: pointer;
  }
 }

 .buttonAddContact {
  margin-bottom: 20px;
  padding: 5px;

  :hover {
   border-radius: 5px;
   cursor: pointer;
   background-color: rgb(111, 111, 250);
   color: white;
  }
 }
`;
