import styled from "styled-components";

export const ButtonStyled = styled.button`
 display: block;
 margin: 0 auto;
 margin-top: 20px;
 margin-bottom: 20px;
 padding: 8px 16px;
 border-radius: 2px;
 background-color: #3f51b5;
 transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);
 text-align: center;
 color: #fff;
 border: 0;
 text-decoration: none;
 cursor: pointer;
 font-family: inherit;
 font-size: 18px;
 line-height: 24px;
 font-style: normal;
 font-weight: 500;
 min-width: 180px;
 box-shadow: 0px 3px 1px -2px rgba(0, 0, 0, 0.2),
  0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 1px 5px 0px rgba(0, 0, 0, 0.12);

 :hover {
  background-color: #18fa24;
  color: black;
 }
 :hover .headerIcon{
     fill: black;
 }
 
 .headerIcon {
  display: inline-block;
  margin-left: 15px;
  width: 20px;
  height: 20px;
  fill: #ffff;
  color: #fff;
 }

 .Button:hover,
 .Button:focus {
  background-color: #303f9f;
  color: black;
 }
`;
